

*{
    box-sizing: border-box;
    margin: 8px;
    justify-content: center;
    
    
}

form input{
    display: block;
    padding:10px;
    margin-top: 10px;
    margin-bottom: 10px;
    border: 2px solid;
    border-radius: 10px;
    width: 25%;
    display: flex;
    justify-content: center;
    
}

form button{
    width: 25%;
    border: 2px solid;
    background-color: black;
    border-radius: 15px;
    font-weight: 600;
    color: white;
    
}

p{
    width: 25%;
    color:black;
    border-radius:10px ;
    text-transform: capitalize;
    
}
.container{
    flex-wrap: wrap;
    background-color:palevioletred;
    width: 100%;
    margin: 20px;
    padding-top: 15px;
    margin-right: 15px;
}